<template>
  <div class="Reading">
    <h1>阅读一下</h1>
    <ul class="Reading_article">
      <li v-for="(item,index) in readingArr" :key="index">
        <div class="Reading_li_wrap">
          <p class="Reading_time"><span class="el-icon-date"></span> {{item.readingTime.toString().split(".")[0]}}</p>
          <p class="Reading_tie" @click="getIntoArt(index)">{{item.readingTitle}} - 点击查看</p>
          <p class="Reading_labe"><span class="el-icon-collection-tag"></span> {{item.readingLabel}}</p>
          <p class="Reading_intro">{{item.readingIntro}}</p>
        </div>
      </li>
      
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Reading',
  data() {
    return {
      readingArr:[],
    }
  },
  methods:{
    async getAllArt(){
      let {data} = await this.$axios.get('/node/admin/getAllArt')
      // console.log(data);
      this.readingArr = data
    },
    getIntoArt(index){
      // console.log(index);
      this.$router.push({path:'/artRoute',query:{data:this.readingArr[index]}})
    }
  },
  created(){
    this.getAllArt()
  }
}
</script>

<style lang="less">
.Reading {
  // height: 200vh;
  // background: pink;
  padding: 10px;

  h1 {
    font-family: "STKaiti";
    padding: 5px;
    background-image: linear-gradient(to right, white, rgb(93, 202, 245));
    margin-right: -12px;
  }

  .Reading_article {
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
    // height: 400px;
    border-radius: 20px;
    // background: pink;
    // background-image: linear-gradient(to bottom left, rgb(149, 225, 255) 0%, white 80%);

    li {
      margin: 20px auto;
      border-radius: 75px 10px 10px 75px;
      width: 70%;
      height: 150px;
      background: skyblue;
      background-image: url('../assets/img/bizhi06.webp');
      background-size: cover;
      background-position: -0px -0px;
      border-left: 2px solid #eee;
      border-bottom: 1px solid #eee;
      box-shadow: 3px 7px 7px 1px rgb(93, 202, 245);

      transition: 0.5s;



      &:hover {
        transition: 0.5s;
        // cursor: pointer;
        // box-shadow: 1px 2px 6px 2px rgb(93, 202, 245);
        transform: translateX(-20px);
      }

      // background-size: ;
      .Reading_li_wrap {
        width: 70%;
        border-radius: 0 10px 10px 0;
        height: 100%;
        //毛玻璃
        filter: blur(0px) opacity(1);
        backdrop-filter: blur(6px);
        clip-path: polygon(10% 0, 100% 0, 100% 100%, 0 100%);
        margin-left: 30%;

        p {
          color: white;
        }

        .Reading_time {
          margin-left: -35%;
        }

        .Reading_tie {
          // margin-left: -0%;
          font-size: 2em;
          font-family: "STKaiti";
          &:hover{
            cursor: pointer;
          }

        }

        .Reading_labe {
          margin-right: -60%;
        }

        .Reading_intro {
          width: 50%;
        }
      }
    }
  }

}
</style>